.sidebaroption {
  display: flex;
  align-items: center;
  cursor: pointer;
}
.sidebaroption:hover {
  background-color: #e8f5fe;
  border-radius: 30px;
  color: var(--twitter-color);
  transition: color 100ms ease-out;
}
.sidebaroption > .MuiSvgIcon-root {
  padding: 20px;
}

.sidebaroption > h1 {
  font-weight: 600;
  font-size: 20px;
  margin-right: 20px;
}
.sidebar--active {
  color: var(--twitter-color);
}
